uetype');\n  font-weight: 600;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Inter';\n  src: url('../../../fonts/inter/Inter-Bold.ttf') format('truetype');\n  font-weight: 700;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Inter';\n  src: url('../../../fonts/inter/Inter-ExtraBold.ttf') format('truetype');\n  font-weight: 800;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Inter';\n  src: url('../../../fonts/inter/Inter-Black.ttf') format('truetype');\n  font-weight: 900;\n  font-style: normal;\n}\n","button{\n  padding: 0;\n  background: transparent;\n  border: none;\n}\nbutton:focus{\n  outline: none;\n}\na{\n  color: inherit;\n  text-decoration: none;\n}",".button {\n  @include themed(color, text);\n  padding: $spacing-regular;\n  @include themed-border('', 1px, dashed, text);\n  cursor: pointer;\n  width: fit-content;\n  font-size: 15px;\n  font-weight: 900;\n  border-radius: $border-radius;\n  transition: all $fast-animation;\n\n  &:hover {\n    @include themed(color, background);\n    @include themed(background, text);\n    @include themed-border('', 1px, dashed, background);\n  }\n}","// ANIMATIONS\n$fast-animation: .2s;\n$normal-animation: .5s;\n$slow-animation: 1s;\n$speed: cubic-bezier(0.95, 0.17, 0, 0.89);\n\n\n// SPACING\n$spacing-xxsmall: 5px;\n$spacing-xsmall: 10px;\n$spacing-small: 15px;\n$spacing-regular: 20px;\n$spacing-large: 30px;\n$spacing-xlarge: 40px;\n$spacing-xxlarge: 60px;\n$section-container-width: 1000px;\n\n// BORDER\n$border-radius: 4px;\n\n// MEDIA SIZES\n$desktop-breakpoint: 1165px;\n$tablet-breakpoint: 600px;","$light:(\n  primary: #f3896f,\n  secondary: #3d5a80,\n  text: #293241,\n  background: #e6e6e6,\n  transparent: #0000\n);\n\n$dark: (\n  primary: #ee886f,\n  secondary: #638bc0,\n  text: #e6e6e6,\n  background: #293241,\n  transparent: #0000\n);\n\n@mixin themed($property, $color) {\n  [data-theme=\"light\"] & {\n    #{$property}: map-get($light, $color);\n  }\n  [data-theme=\"dark\"] & {\n    #{$property}: map-get($dark, $color);\n  }\n}\n\n@mixin themed-border($style, $width, $type, $color) {\n  [data-theme=\"light\"] & {\n    border-#{$style}: $width $type map-get($light, $color) ;\n    border#{$style}: $width $type map-get($light, $color) ;\n  }\n  [data-theme=\"dark\"] & {\n    border-#{$style}: $width $type map-get($dark, $color);\n    border#{$style}: $width $type map-get($dark, $color);\n  }\n}\n\n@mixin themed-dots($color1, $color2, $size) {\n  [data-theme=\"light\"] & {\n    background-image: radial-gradient(\n      map-get($light, $color1) $size, map-get($light, $color2) $size\n    );\n  }\n  [data-theme=\"dark\"] & {\n    background-image: radial-gradient(\n      map-get($dark, $color1) $size, map-get($dark, $color2) $size\n    );\n  }\n}\n\n\n@mixin themed-line($color) {\n  [data-theme=\"light\"] & {\n    background-image: linear-gradient(90deg, transparent 50%, transparent 50%), linear-gradient(90deg, transparent 50%, transparent 50%), linear-gradient(0deg, map-get($light, $color) 50%, transparent 50%);\n  }\n  [data-theme=\"dark\"] & {\n    background-image: linear-gradient(90deg, transparent 50%, transparent 50%), linear-gradient(90deg, transparent 50%, transparent 50%), linear-gradient(0deg, map-get($dark, $color) 50%, transparent 50%);\n  }\n}\n\n@mixin themed-borders($color) {\n  [data-theme=\"light\"] & {\n    background-image: linear-gradient(90deg, map-get($light, $color) 50%, transparent 50%), linear-gradient(90deg, map-get($light, $color) 50%, transparent 50%), linear-gradient(0deg, map-get($light, $color) 50%, transparent 50%), linear-gradient(0deg, map-get($light, $color) 50%, transparent 50%);\n  }\n  [data-theme=\"dark\"] & {\n    background-image: linear-gradient(90deg, map-get($dark, $color) 50%, transparent 50%), linear-gradient(90deg, map-get($dark, $color) 50%, transparent 50%), linear-gradient(0deg, map-get($dark, $color) 50%, transparent 50%), linear-gradient(0deg, map-get($dark, $color) 50%, transparent 50%);\n  }\n}\n\n@mixin themed-gradiant-shadow($angle, $color) {\n  [data-theme=\"light\"] & {\n    background: linear-gradient($angle, map-get($light, $color) 6%, rgba(0,0,0,0) 100%);\n  }\n  [data-theme=\"dark\"] & {\n    background: linear-gradient($angle, map-get($dark, $color) 6%, rgba(0,0,0,0) 100%);\n  }\n}",".common-switch {\n  position: relative;\n  display: inline-block;\n  width: $spacing-xxlarge;\n  height: 34px;\n\n  &__slider {\n    user-select: none;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    position: absolute;\n    cursor: pointer;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    @include themed(background-color, 'text');\n    -webkit-transition: .4s;\n    transition: .4s;\n    border-radius: 34px;\n  }\n  \n  &__slider:before {\n    position: absolute;\n    content: '';\n    height: 26px;\n    width: 26px;\n    left: 4px;\n    bottom: 4px;\n    @include themed(background-color, 'background');\n    -webkit-transition: .4s;\n    transition: .4s;\n    border-radius: 50%;\n  }\n\n  &__checkbox { \n    opacity: 0;\n    width: 0;\n    height: 0;\n\n    &:checked + .common-switch__slider:before {\n      -webkit-transform: translateX(26px);\n      -ms-transform: translateX(26px);\n      transform: translateX(26px);\n    }\n\n    &:checked + .common-switch__slider {\n      @include themed(background-color, 'text');\n    }\n  }\n}",".logo {\n  &--big {\n    width: $spacing-xxlarge;\n  }\n\n  &--small {\n    width: $spacing-large;\n  }\n\n  &--xsmall {\n    width: $spacing-regular;\n  }\n}",".link {\n  position: relative;\n  width: fit-content;\n\n  &::before {\n    content: '';\n    position: absolute;\n    width: 100%;\n    height: 4px;\n    @include dotted-background(text, transparent, 33%, 4px);\n    bottom: -2px;\n    left: 0;\n    transform-origin: right;\n    transform: scaleX(0);\n    transition: transform $fast-animation ease-in-out;\n  }\n\n  &[data-type=\"regular\"] {\n    &:hover::before {\n      transform-origin: left;\n      transform: scaleX(1);\n    }\n  }\n}","@mixin dotted-moved-background($color1, $color2, $percentage, $size) {\n  & {\n    position: relative;\n    display: inline-block;\n  }\n  &::before {\n    content: '';\n    @include themed-dots( $color1, $color2, $percentage);\n    background-size: $size $size;\n    height: 50%;\n    width: 100%;\n    position: absolute;\n    top: 55%;\n    z-index: -1;\n    left: 5%;\n    background-repeat: space;\n  }\n}\n\n@mixin dotted-shadow($color, $separation) {\n  & {\n    position: relative;\n  }\n  &::after {\n    content: '';\n    @include dotted-background($color, transparent, 50%, $spacing-xxsmall);\n    border-radius: 0 $border-radius $border-radius $border-radius;\n    height: 100%;\n    width: 100%;\n    position: absolute;\n    left: $separation;\n    top: $separation;\n    z-index: -1;\n    background-repeat: space;\n    transition: all $fast-animation;\n  }\n}\n\n@mixin dotted-background($color1, $color2, $percentage, $size) {\n  & {\n    @include themed-dots( $color1, $color2, $percentage);\n    background-size: $size $size;\n  }\n}\n\n@mixin dotted-clip($color1, $color2, $percentage, $size) {\n  background-clip: text;\n  -webkit-background-clip: text;\n  margin: $spacing-xxsmall 0;\n  color: transparent;\n  @include themed-dots( $color1, $color2, $percentage);\n  background-size: $size $size;\n}\n\n@mixin shadowed-box {\n  font-size: 20px;\n  padding: $spacing-small;\n  border-radius: $border-radius;\n  @include dotted-shadow(secondary);\n  @include themed-border('', 1px, dashed, secondary);\n  @include themed(background-color, background);  \n  margin-bottom: $spacing-regular;\n}\n// TODO: make themed\n@mixin conic-clip($color1, $color2, $percentage, $size) {\n  background-clip: text;\n  -webkit-background-clip: text;\n  margin: $spacing-xxsmall 0;\n  color: transparent;\n  background-image: repeating-conic-gradient(\n    at 0% 100%,\n    #ee6c4d 0deg 5deg,\n    #3d5a80 10deg 15deg\n  );\n}\n\n@mixin tablet-width() {\n  @media only screen and (max-width: $desktop-breakpoint) {\n    @content;\n  }\n}\n\n@mixin mobile-width() {\n  @media only screen and (max-width: $tablet-breakpoint) {\n    @content;\n  }\n}",".list {\n  font-size: 15px;\n  li {\n    margin-bottom: $spacing-xsmall;\n    list-style-type: '⨳';\n    span {\n      position: relative;\n      left: $spacing-xsmall;\n    }\n  }\n\n  [data-bullet-color=\"background\"] {\n    &::marker {\n      @include themed(color, secondary);\n    }\n  }\n\n\n  [data-bullet-color=\"text\"] {\n    &::marker {\n      @include themed(color, text);\n    }\n  }\n\n}",".animation-block {\n  width: 250px;\n  height: 50px;\n  position: absolute;\n  transform: rotate(90deg);\n  overflow: hidden;\n\n  @include mobile-width() {\n    display: none;\n  }\n\n  &__animation {\n    height: 100%;\n    position: absolute;\n    width: 0px;\n    animation: movement 4s $speed forwards;\n    @include dotted-background(text, transparent, 60%, 4px);\n    @include themed(color, background);\n    font-weight: 900;\n    font-size: 40px;\n  }\n\n  &::after {\n    content: '';\n    position: absolute;\n    @include themed-border('top', 26px, solid, background);\n    @include themed-border('bottom', 26px, solid, background);\n    @include themed-border('left', 20px, solid, transparent);\n    right: 0;\n  }\n}\n\n@keyframes movement {\n  0% {\n    width: 0px;\n    left: 0;\n  }\n  50% {\n    width: 250px;\n    right: 0;\n  }\n  100% {\n    width: 0px;\n    right: 0;\n    display: none;\n  }\n}\n",".tab-header {\n  font-weight: 900;\n  font-size: 25px;\n  border-radius: $border-radius;\n  padding: $spacing-xxsmall;\n  cursor: pointer;\n  @include themed(color, text);\n\n  &[data-selected=\"true\"] {\n    @include themed(color, background);\n    @include dotted-background(secondary, transparent, 80%, 4px);\n  }\n}\n\n.tab-content {\n  padding: $spacing-regular;\n}",".header {\n  grid-area: header;\n}\n\n.main {\n  grid-area: main;\n}\n\n.sidebar {\n  grid-area: sidebar;\n}\n\n.footer {\n  grid-area: footer;\n}\n\n.app-container {\n  display: grid;\n  grid-gap: $spacing-xsmall;\n  min-height: 100vh;\n  grid-template-rows: auto;\n  grid-template-columns: 4fr 1fr;\n  grid-template-areas:\n    \"header header\"\n    \"main main\"\n    \"main main\"\n    \"footer footer\";\n}\n","$ball-width: $spacing-regular;\n$ball-radius: $ball-width / 2;\n\n.timeline {\n  display: flex;\n  flex-direction: column;\n\n  &__content {\n    margin: 0 $spacing-xsmall $spacing-xsmall;\n\n    &-text {\n      padding: $spacing-xsmall $spacing-small $spacing-xsmall $spacing-xsmall;\n      margin: $spacing-xsmall 0;\n      max-width: 500px;\n      border-radius: 0 $border-radius $border-radius $border-radius;\n      @include themed-border('', 1px, dashed, primary);\n      @include themed(background-color, background);\n      @include dotted-shadow(primary, $spacing-xsmall);\n    }\n\n    &-header {\n      font-size: 15px;\n      font-weight: 200;\n    }\n\n    &-title {\n      font-size: 20px;\n      font-weight: 600;\n      margin-bottom: $spacing-xsmall;\n    }\n\n    &-position {\n      font-weight: 200;\n    }\n  }\n\n  &__step {\n    position: relative;\n    display: flex;\n\n    &:last-of-type {\n      .timeline__ball {\n        &::before{\n          content: '';\n          @include themed-gradiant-shadow(0deg, background);\n          position: absolute;\n          left: 7px;\n          top: 1px;\n          content: '';\n          width: 5px;\n          height: 100%;\n        }\n      }\n    }\n\n    &:first-of-type {\n      .timeline__ball {\n        &::before{\n          content: '';\n          @include themed-gradiant-shadow(180deg, background);\n          position: absolute;\n          left: 7px;\n          top: -50px;\n          content: '';\n          width: 5px;\n          height: 100px;\n          z-index: -1;\n        }\n        &:after{\n          position: absolute;\n          content: '';\n          height: 120%;\n          left: $ball-radius - 1px;\n          top: -50px;\n          z-index: -2;\n        }\n      }\n    }\n  }\n\n  &__ball {\n    min-width: $ball-width;\n    height: $ball-width;\n    border-radius: 50%;\n    @include themed(background-color, secondary);\n  \n    &:after{\n      position: absolute;\n      content: '';\n      width: 1px;\n      height: 100%;\n      left: $ball-radius - 1px;\n      top: 1px;\n      z-index: -1;\n      @include themed(background-color, background);\n      @include themed-line(text);\n      background-repeat: repeat-x, repeat-x, repeat-y;\n      background-size: 15px 1px, 15px 1px, 1px 15px, 1px 15px;\n      background-position: left top, right bottom, left bottom, right top;\n      animation: line-animation 1s infinite linear;\n    }\n  }\n}\n\n@keyframes line-animation {\n  0% {\n    background-position: left top, right bottom, left bottom, right top;\n  }\n  100% {\n    background-position: left 15px top, right 15px bottom , left bottom 15px , right   top 15px;\n  }\n}\n","$sidebar-padding: $spacing-small;\n\n.sidebar {\n  position: fixed;\n  height: calc(100vh - #{$sidebar-padding} * 2);\n  display: flex;\n  justify-content: space-between;\n  flex-direction: column;\n  padding: $sidebar-padding;\n\n  &__section {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n\n    .link {\n      margin-bottom: $spacing-regular;\n    }\n  }\n\n  &__visible-page {\n    writing-mode: vertical-rl;\n    display: grid;\n    place-items: center;\n    font-weight: 800;\n    letter-spacing: $spacing-small;\n    animation:none;\n  }\n\n  @include tablet-width() {\n    display: none;\n  }\n}\n\n.animate-out {\n  animation: animate-page-out $fast-animation ease;\n}\n\n.animate-in {\n  animation: animate-page-in $fast-animation ease;\n}\n\n@keyframes animate-page-out {\n  0% {\n    letter-spacing: $spacing-xsmall;\n  }\n  100% {\n    letter-spacing: -#{$spacing-xsmall};\n  }\n}\n\n@keyframes animate-page-in {\n  0% {\n    letter-spacing: -#{$spacing-xsmall};\n  }\n  100% {\n    letter-spacing: $spacing-small;\n  }\n}",".main {\n  overflow: hidden;\n  &>div {\n    margin: 0 auto;\n    margin-bottom: 150px;\n    max-width: $section-container-width;\n    display: flex;\n    justify-content: center;\n    min-height: 75vh;\n\n    @include tablet-width() {\n      margin-right: $spacing-large;\n      margin-left: $spacing-large;\n      flex-direction: column;\n      align-items: center;\n      max-width: none;\n    }\n  }\n}",".title {\n  font-size: 80px;\n  font-weight: 900;\n  text-transform: uppercase;\n  margin: 0;\n  @include dotted-moved-background(secondary, transparent, 50%, 6px);\n\n  &__sub-title {\n    margin: 0;\n    font-size: 40px;\n    font-weight: 600;\n    @include dotted-clip(primary, transparent, 70%, 3px);\n  }\n\n  @include mobile-width() {\n    font-size: 55px;\n    &__sub-title {\n      font-size: 25px;\n    }\n  }\n}\n",".description {\n  font-weight: 600;\n  font-size: 18px;\n}",".youtube-info  {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  white-space: nowrap;\n  justify-content: space-evenly;\n  overflow: hidden;\n  align-items: center;\n\n  &__title {\n    font-weight: 700;\n    margin-bottom: $spacing-xsmall;\n  }\n\n  &__number {\n    font-weight: 700;\n    font-size: 25px;\n    @include themed(color, primary);  \n  }\n\n  &__data {\n    position: relative;\n    width: fit-content;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n  }\n}\n",".box {\n  &__container {\n    left: 0;\n    top: 0px;\n    position: absolute;\n    width: 200px;\n    height: 200px;\n    justify-content: center;\n    align-items: center;\n    border-radius: $border-radius;\n    @include dotted-shadow(primary, 0px);\n    @include themed(background-color, background);\n    @include themed-border(\"\", 1px, dashed, primary);\n    margin: $spacing-regular;\n    transition: all $fast-animation;\n    padding: $spacing-regular;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n\n    &:hover {\n      left: -2px;\n      top: -2px;\n      @include dotted-shadow(primary, $spacing-xsmall);\n    }\n\n    .logo {\n    }\n  }\n\n  &__tags {\n    display: flex;\n    flex: 1;\n    right: 300;\n    justify-content: flex-start;\n    align-items: center;\n  }\n\n  &__tag {\n    font-size: 10px;\n    margin-left: $spacing-xsmall;\n    @include themed(color, primary);\n\n    &:first-child {\n      margin: 0;\n    }\n  }\n\n  &__title {\n    font-weight: 800;\n  }\n}\n",".footer {\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n  align-items: center;\n\n  &__container {\n    height: 200px;\n    display: flex;\n    justify-content: center;\n    font-weight: 200;\n    flex-direction: column;\n    align-items: center;\n    width: $section-container-width;\n    max-width: $section-container-width;\n    border-top: 1px dashed;\n\n    @include tablet-width() {\n      width: 100%;\n      max-width: none;\n    }\n  }\n  .palette {\n    margin-top: $spacing-small;\n  }\n\n  &__credits {\n    font-size: 10px;\n  }\n}\n",".palette {\n  display: flex;\n\n  &>div {\n    width: $spacing-xlarge;\n    height: $spacing-regular;\n  }\n  .primary {\n    @include themed(background-color, primary);\n  }\n  .secondary {\n    @include themed(background-color, secondary);\n  }\n  .text {\n    @include themed(background-color, text);\n  }\n  .background {\n    @include themed(background-color, background);\n  }\n}",".home {\n  height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  .home-container {\n    .animation-block {\n      bottom: 150px;\n      right: 0;\n    }\n  }\n}","$concept-width: 175px;\n$moving-border-width: 150px;\n\n.whoami {\n  display: flex;\n  justify-content: flex-start;\n  margin: 0 auto;\n  max-width: 800px;\n\n  &__container {\n    display: flex;\n    width: 770px;\n    flex-direction: column;\n    margin-right: 20px;\n\n    .title__sub-title {\n      margin-bottom: $spacing-regular;\n    }\n\n    p:last-of-type {\n      margin-bottom: $spacing-xlarge;\n    }\n\n    b {\n      @include themed(color, primary);\n    }\n\n    @include tablet-width() {\n      text-align: center;\n      width: auto;\n\n      .link {\n        margin: 0 auto;\n      }\n    }\n  }\n\n  &__moving-border {\n    width: 0px;\n    max-height: 0px;\n    position: relative;\n    @include themed-borders(text);\n    background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;\n    background-size: 15px 1px, 15px 1px, 1px 15px, 1px 15px;\n\n    padding: 1px 0 1px 1px;\n\n    &[data-animate=\"true\"] {\n      animation: initial-border-animation 1.5s $speed forwards,\n        border-animation 1.5s 1.5s $speed infinite;\n\n      @include tablet-width() {\n        animation: initial-border-animation-small 1.5s $speed forwards,\n          border-animation 1.5s 1.5s $speed infinite;\n      }\n    }\n    animation-direction: alternate;\n\n    &:hover {\n      animation-duration: 0.5s;\n    }\n\n    @include tablet-width() {\n      margin-top: 80px;\n      background-size: 15px 1px, 15px 0px, 0px 15px, 0px 15px;\n    }\n  }\n\n  &__concept {\n    display: grid;\n    place-items: center;\n    position: absolute;\n    width: $concept-width;\n    height: $concept-width;\n    border-radius: 50%;\n    @include themed(color, background);\n    @include dotted-background(primary, text, 70%, 4px);\n    font-weight: 700;\n    font-size: 25px;\n\n    &--design {\n      top: -#{$concept-width / 2};\n      left: -#{$concept-width / 2};\n    }\n\n    &--coding {\n      bottom: -#{$concept-width / 2};\n      right: -#{$concept-width / 2};\n    }\n  }\n\n  @include tablet-width() {\n    margin-bottom: $spacing-large;\n  }\n}\n\n@keyframes border-animation {\n  0% {\n    background-position: 0px 0px, 0px 100%, 0px 0px, $moving-border-width 0px;\n  }\n  100% {\n    background-position: $moving-border-width 0px, $moving-border-width 